body, html{
  margin:0;
  padding:0;
}


.card-container{
  width: 100vw;
  display:flex;
  flex-wrap:wrap;
  justify-content: center;
  gap:20px;
  margin-bottom:10%;
}

.svg-inline {
  height: 50px;
  width:50px;
  overflow: visible;
  vertical-align: -.125em;
}

.buttons{
  width:100%;
  display:flex;
  justify-content: space-around;
  align-items: center;
}

.like{
  background-image: url("../assets/heart.svg");
  filter:invert(50%);
  margin-bottom:-3px;
}

.liked .like{
  filter:none;
}

.delete{
  background-image: url("../assets/delete.svg");
}

.like, .delete{
  cursor: pointer;
}

#filter{
  height:10vh;
  width: 100%;
  display:flex;
  align-items: center;
  justify-content: center;
  margin-bottom:5vh;
  cursor:pointer;
}

#filter.active{
  color:orange;
}

#filter.active div{
  border-color:orange;
}

#filter div{
  display:inline-block;
  border:solid black 1px;
  padding: 10px 20px;
  font-size:2rem;
}